import React from 'react';
import Row from './Row.js';

class Grid extends React.Component{
  constructor(props){
    super(props);
    this.width = 8;
    this.height = 8;
    this.state = {
          cells: Array(this.props.rows).fill(Array(this.props.colls).fill(false)),
          day: 0,
    }
  }

  insertRows(){
    let rows = [];

    for (var i = 0; i < this.props.rows; i++) {
      rows.push(<Row
        clickCell = {this.clickCell.bind(this)}
        id={i}
        key={i}
        elements={this.state.cells[i]}/>);
    }
    return rows;
  }

  clickCell(e){

    let newCells = JSON.parse(JSON.stringify(this.state.cells));
    let x = e.target.getAttribute('id').split('_')[0]
    let y = e.target.getAttribute('id').split('_')[1]
    newCells[x][y] = !newCells[x][y];
    this.setState({
      cells: newCells
    })
  }

  click(){

    this.setState({
      day: this.state.day+1,
      cells: this.newDay(this.state.cells),
    })
  }

  newDay(array){
    let next = n => {
      if(n < this.props.colls-1) return n+1;
      else return 0;
    };
    let prev = (n)=> {
      if(n<=0) return this.props.colls-1;
      else return n-1;
    }
    let getNaigbors = (x, y) => {
      let naigbors = [];
      let n1 = array[next(x)][y];
      let n2 = array[next(x)][next(y)];
      let n3 = array[x][next(y)];
      let n4 = array[prev(x)][next(y)];
      let n5 = array[prev(x)][y];
      let n6 = array[prev(x)][prev(y)];
      let n7 = array[x][prev(y)];
      let n8 = array[next(x)][prev(y)];
      naigbors.push(n1);
      naigbors.push(n2);
      naigbors.push(n3);
      naigbors.push(n4);
      naigbors.push(n5);
      naigbors.push(n6);
      naigbors.push(n7);
      naigbors.push(n8);
      return naigbors;
    }
    function isLife(x,y){
      let naigbors = getNaigbors(x, y);
      let nNaigbors = naigbors.reduce((a,b)=>a+b);
      if( !array[x][y] && (nNaigbors === 3) ) {
        console.log('this.not run');
        return true;
      }
      else if ( array[x][y] &&  nNaigbors!==2 && nNaigbors!==3){
        console.log('asdf');
        return false;
      }
      else if( array[x][y] && (nNaigbors===2 || nNaigbors===3))return true;
      else {
        console.log('3');
        return false};

    }

    let newArray = JSON.parse(JSON.stringify(this.state.cells));
    for (let i = 0; i < array.length; i++) {
      for (let j = 0; j < array[i].length; j++) {
        newArray[i][j]=isLife(i,j)
      }
    }
    return newArray;
  }

  run = ()=>{setInterval(this.click.bind(this), 200)}

  render(){
    return(
      <>
        <div
          width={this.props.width*this.props.rows+'px'}
          height={this.props.height * this.props.colls+'px'}
          >
          {this.insertRows()}
        </div>
        <div className={'buttons'}>
          <button onClick={this.click.bind(this)}>step</button>
          <button onClick={this.run}>run</button>
        </div>
        <h4>{this.state.day}</h4>
      </>
    )
  }
}

export default Grid;
