import React from 'react';

class Row extends React.Component{
  constructor(props){
    super(props);
  }
  pushElem(){

    let elems = [];
    for (let i = 0; i < this.props.elements.length; i++) {
      elems.push(<div
        className={'element ' + (this.props.elements[i] ? 'life' : 'dead')}
        id={this.props.id+'_'+i}
        key={this.props.id+'_'+i}
        onClick={this.props.clickCell}

        >

        </div>)
    }
    return(elems);
  }
  render(){
    return(
      <div
        className={'row'}
      >{this.pushElem()}</div>
    )
  }
}

export default Row;
